.select{
  position: relative;
  max-width: 430px;

  &__header{
    margin-top: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid #64626A;
    padding: 18px 20px;
    cursor: pointer;

    &:focus{
      outline: 2px solid var(--swiper-theme-color);
    }

    &:hover{
      background: var(--base-bg-inputs);
    }
  }

  &__current{
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    letter-spacing: 0.01em;
    color: #b3b1b7;
  }

  &__svg{
    width: 13px;
    height: 13px;
    transform: rotate(90deg);
    fill: #64626A;
  }

  &__body{
    background-color: #fff;
    position: absolute;
    left: 0;
    right: 0;
    top: 100%;
    margin: 0;
    padding: 0;
    border: 1px solid #64626A;
    border-top: none;
    z-index: 1;
    display: none;
  }

  &__item{
    padding: 18px 20px;
    cursor: pointer;

    &:hover{
      background: var(--base-bg-inputs);
    }

    &:focus{
      outline: 2px solid var(--swiper-theme-color);
    }
  }

  &__item:not(:last-child){
    border-bottom: 1px solid #64626A;;
  }
}

.select.select--active .select__body{
  display: block;
}

.select.select--active .select__svg{
    transform: rotate(270deg);
}

@media (max-width: 1000px) {
  .title-1{
    font-size: 45px;
  }

  .select{
    max-width: 1000px;
  }
}
