.footer {
  background: var(--global-dark);
  padding-top: 70px;
  padding-bottom: 45px;
  color: var(--base-text-on-dark);

  &__container {
    padding: 0 2px;
  }

  &__top {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 25px;
    border-bottom: 2px solid #ffffff30;
  }

  &__left {
    max-width: 425px;
    padding-top: 3px;
  }

  &__logo {
    font-family: var(--second-family);
    font-weight: 800;
    font-size: 24px;
    line-height: 100%;
    align-items: center;
    color: var(--base-text-on-dark);
    transition: outline 0.3ms ease-in-out, color 0.3ms ease-in-out;
    display: inline-block;
    max-width: 108px;
    height: 30px;
  }

  &__title {
    margin-top: 33px;
    color: inherit;
  }

  &__text {
    margin-top: 6px;
  }

  &__link {
    margin-top: 7px;
    background: none;
    padding: 5px 10px 5px 0;

    &:focus {
      outline: 2px solid var(--swiper-theme-color);
      background: none;

      .svg {
        background: none;
      }
    }

    &:hover {
      background: none;

      .svg {
        background: none;
      }
    }

    &:active {
      outline: none;
    }

    .svg {
      background: none;
    }
  }

  &__right {
    max-width: 570px;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;
    row-gap: 30px;
  }

  &__bottom {
    padding-top: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__span {
    max-width: 390px;
  }
}

@media (max-width: 1440px) {
  .footer {
    &__left {
      max-width: 300px;
    }
  }
}

@media (max-width: 1000px) {
  .footer {
    &__right {
      max-width: 290px;
    }
  }
}

@media (max-width: 768px) {
  .footer {
    &__top {
      row-gap: 30px;
    }

    &__left {
      max-width: 768px;
    }

    &__right {
      max-width: 768px;
      justify-content: center;
    }

    .nav__list {
      row-gap: 5px;
      column-gap: 28px;
      justify-content: center;
    }

    &__bottom {
      flex-direction: column;
      row-gap: 30px;
    }
  }
}
