.form {
  &__block {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }

  &__label {
    display: flex;
    flex-direction: column;
  }

  &__span {
    max-width: 83px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 0.01em;
    color: #000;
    position: relative;

    &::after {
      content: "*";
      position: absolute;
      top: 0;
      right: -10px;
      color: var(--global-primary);
    }
  }

  &__select {
    margin-top: 25px;
  }

  &__span--width {
    max-width: 73px;
  }

  &__label-flex {
    margin-top: 24px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
  }

  &__inp {
    margin-top: 11px;
    padding: 17px;
    border: 1px solid var(--base-boarder);
    max-width: 204px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    letter-spacing: 0.01em;
    color: #b3b1b7;

    &:focus {
      outline: 2px solid var(--swiper-theme-color);
    }

    &:hover {
      background: var(--base-bg-inputs);
    }
  }

  &__btn {
    margin-top: 45px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    letter-spacing: 0.01em;
    text-align: center;
    color: var(--base-text-on-dark);
    background: var(--global-primary);
    width: 100%;
    padding: 17px;
    border: none;
    transition: background 0.3ms ease-in-out, color 0.3ms ease-in-out,
      outline 0.3ms ease-in-out;

    &:focus {
      outline: 2px solid var(--global-primary);
      background: var(--global-hover-secondary);
      color: var(--base-text-on-dark);
    }

    &:hover {
      outline: none;
      background: var(--global-hover-alt);
      color: var(--base-text-on-dark);
    }

    &:active {
      outline: none;
      color: var(--global-hover-alt);
      background: unset;
    }
  }

  .comment {
    min-height: 122px;
    width: 100%;
    max-width: 430px;
  }
}

@media (max-width: 1000px) {
  .form {
    &__label {
      flex-wrap: wrap;
      width: 340px;
      max-width: 100%;
    }

    &__inp {
      max-width: 340px;
    }

    .form__label-flex {
      width: 1000px;
      max-width: 100%;
    }

    .comment {
      max-width: 1000px;
    }
  }
}

@media (max-width: 768px) {
  .form {
    &__label {
      width: 1000px;
      max-width: 100%;
    }

    &__inp {
      width: 1000px;
      max-width: 100%;
    }
  }
}

@media (max-width: 564px) {
  .form {
    &__block {
      flex-wrap: wrap;
    }

    &__inp {
      max-width: 100%;
    }
  }
}
