.info{

  &__content{
    display: flex;
    column-gap: 30px;
    justify-content: space-between;
  }

  &__list{
    max-width: 410px;
  }

  &__block{
    max-width: 510px;
  }

  &__text{
    margin-top: 16px;
  }

  &__link{
    margin-top: 30px;
    border-radius: 1px;
    padding: 15px 25px;
    max-width: 213px;
    height: 56px;
  }
}


@media (max-width: 1000px) {
  .info{

    &__content{
      flex-direction: column-reverse;
      row-gap: 60px;
    }

    &__list{
      max-width: 1000px;
    }

    &__block{
      max-width: 1000px;
    }
  }
}

