.burger{
  display: none;
  width: 25px;
  height: 20px;
  position: relative;
  color: inherit;
  cursor: pointer;

  &__line{
    position: absolute;
    left: 0;
    width: 25px;
    height: 2px;
    background-color: var(--base-bg-dark);
    transition: transform .5s ease-in-out;
  }

  &__line:nth-child(1){
    transform: translateY(8px);
  }

  &__line:nth-child(3){
    transform: translateY(-8px);
  }
}

@media (max-width: 768px) {
  .burger{
    display: block;
    z-index: 2;
  }

  .burger-menu {
    z-index: 1;
    position: absolute;
    top: 0;
    left: -100%;
    padding: 20px;
    background-color: var(--base-text-on-dark);
    width: 100%;
    transition: transform .5s ease-in-out;
  }

  .nav__list--burger{
    flex-direction: column;
    gap: 20px;
    height: 100vh;
  }

  .header.open .burger-menu{
    transform: translateX(100%);
  }

  .header.open .burger__line:nth-child(1){
    transform: translateY(0) rotate(-45deg);
  }

  .header.open .burger__line:nth-child(2){
    transform: translateY(0) rotate(45deg);
  }

  .header.open .burger__line:nth-child(3){
    transform: translateY(0) rotate(45deg);
  }

}
