html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, --second-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  max-width: 1920px;
  margin: 0;
  min-width: 320px;
  background-color: var(--base-text-on-dark);
  color: var(--base-text-secondary);
}

.img {
  background: var(--base-bg-secondary);
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

.link {
  text-decoration: none;
  cursor: pointer;
}

.container {
  overflow: hidden;
  margin: 0 auto;
  max-width: 1200px;
  padding: 0 2px;
}

.is-hidden {
  display: none !important;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  //max-width: var(--container-width);
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: relative;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.hidden-text {
  overflow: hidden;
  display: -webkit-box;
  max-width: 100px;
  -webkit-line-clamp: 2; /* Колличество строк для показа */
  -webkit-box-orient: vertical;
}

.background__video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

svg {
  width: 100%;
  height: 100%;
}

@media (max-width: 1440px) {
  .container {
    max-width: 900px;
  }
}

@media (max-width: 1000px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 564px;
  }
}

@media (max-width: 564px) {
  .container {
    max-width: 100%;
    margin: 0 15px;
  }
}
