.partner {
  background: var(--base-bg-secondary);

  &__container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }

  &__content {
    max-width: 1100px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    align-content: center;
    padding-top: 100px;
    padding-bottom: 45px;
    border-bottom: 2px solid var(--base-boarder);
  }

  &__title {
    max-width: 920px;
    text-align: center;
  }

  &__text {
    max-width: 825px;
    margin-top: 25px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 156%;
    letter-spacing: -0.02em;
    text-align: center;
    color: var(--base-text-secondary);
    text-align: center;
  }

  &__item {
    padding: 2px 0;
  }

  &__link {
    margin-top: 60px;
    padding: 15px 25px;
    max-width: 198px;
  }

  &__partners {
    padding-top: 50px;
    padding-bottom: 100px;
  }
}
