.contact {
  background-color: var(--base-bg-secondary);
  margin: 0;
  position: relative;

  &__container {
    padding: 110px 2px 75px;
    display: flex;
    flex-wrap: wrap;
  }

  &__info{
    max-width: 480px;
  }

  &__text {
    margin-top: 30px;
    color: #64626a;
  }

  &__text--mar{
    margin-top: 16px;
  }

  &__contacts {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
    margin-top: 100px;
  }

  &__link {
    display: flex;
    flex-wrap: wrap;
    transition: background .3ms ease-in-out, color .3ms ease-in-out, border .3ms ease-in-out, outline .3ms ease-in-out,;

    &:focus{
      border: 1px solid var(--base-text-on-dark);
      outline: 2px solid var(--global-primary);
      background: var(--global-hover-secondary);
      color: var(--base-text-on-dark);
    }

    &:hover{
      background: var(--global-hover-alt);
      color: var(--base-text-on-dark);
      border: 1px solid var(--global-hover-alt);
    }

    &:active{
      outline: none;
      color: var(--global-hover-alt);
      background: unset;
      border: 1px solid var(--base-bg-secondary);
    }
  }

  &__link:last-child{
    background: var(--base-bg-primary);
    color: var(--global-primary);
    border: 1px solid var(--global-primary);
    transition: background .3ms ease-in-out, color .3ms ease-in-out, border .3ms ease-in-out, outline .3ms ease-in-out,;

    &:focus{
      border: 1px solid var(--base-text-on-dark);
      outline: 2px solid var(--global-primary);
      background: var(--global-hover-secondary);
      color: var(--base-text-on-dark);
    }

    &:hover{
      background: var(--global-hover-alt);
      color: var(--base-text-on-dark);
      border: 1px solid var(--global-hover-alt);
    }

    &:active{
      outline: none;
      color: var(--global-hover-alt);
      background: unset;
      border: 1px solid var(--base-bg-secondary);
    }
  }

  &__block{
    background-color: var(--base-text-on-dark);
  }

  &__connection{
    position: absolute;
    right: 35%;
    top: 0;
    transform: translate(60%, 20%);
    max-width: 490px;
    //min-height: 662px;
    border: 1px solid var(--base-boarder);
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    background: var(--base-text-on-dark);
    padding: 35px 30px 25px;
  }

  &__form{
    margin-top: 40px;
  }

  &__partners{
    margin-top: 60px;
    padding: 0 3px;
    position: relative;
    max-width: 500px;

    svg {
      max-width: 100px;
      fill: #3b3a40;
    }
  }
}



@media (max-width: 1440px) {
  .contact{

    &__info{
      max-width: 400px;
    }

    &__partners{
      max-width: 400px;


      .partners__list{
        justify-content: center;
      }
    }
  }
}

@media (max-width: 1000px) {
  .contact{

    &__container{
      padding: 75px 2px 75px;
      row-gap: 30px;
    }

    &__contacts{
      margin-top: 30px;
    }

    &__info{
      max-width: 1000px;
    }

    &__connection{
      position: static;
      right: 0;
      top: 0;
      width: 1000px;
      max-width: 100%;
      transform: translate(0%, 0%);
      z-index: 0;
    }

    &__partners{
      max-width: 1000px;
      margin-top: 30px;
    }
  }
}

@media (max-width: 564px) {
  .contact{

    &__container {
      padding: 50px 2px;
    }

    &__contacts{
      gap: 15px;
      flex-wrap: wrap;
    }

    &__link{
      flex-direction: column;
      align-items: flex-start;
      width: 160px;
      max-width: 100%;
    }
  }
}

