.story {
  &__swiper {
    background: var(--base-bg-secondary);
    border-radius: 3px;
    padding: 120px 0 115px 175px;
    width: 100%;
    min-height: 700px;
    position: relative;
  }

  &__article {
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 64px;
  }

  &__left {
    max-width: 580px;
  }

  &__text {
    margin-top: 32px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 42px;
    line-height: 124%;
    letter-spacing: -0.02em;
    color: var(--base-text-secondary);
  }

  &__link {
    &:focus {
      outline: 2px solid var(--global-hover-primary);

      svg {
        fill: var(--global-hover-secondary);
      }
    }
  }

  &__link--bc {
    background: none;
    display: block;
    max-width: 255px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 162%;
    letter-spacing: -0.02em;
    color: var(--global-primary);
    transition: outline 0.3ms ease-in-out, background 0.3ms ease-in-out,
      color 0.3ms ease-in-out;

    &:focus {
      outline: 2px solid var(--global-primary);
      background: var(--global-hover-secondary);
      color: var(--base-text-on-dark);

      .svg-next {
        background: var(--global-hover-secondary);
        fill: var(--base-text-on-dark);
      }
    }

    &:hover {
      color: var(--base-text-on-dark);
      background: var(--global-primary);

      .svg-next {
        fill: var(--base-text-on-dark);
        background: var(--global-primary);
        transform: translateX(10px);
      }
    }

    &:active {
      outline: none;
      color: var(--global-hover-alt);

      .svg-next {
        fill: var(--global-hover-alt);
      }
    }

    svg {
      padding-left: 10px;
      width: 30px;
      height: 10px;
      fill: var(--global-primary);
    }
  }

  &__svg {
    margin-top: 32px;
    fill: var(--base-boarder);
    width: 100px;
    height: 30px;
    transition: fill 0.3ms ease-in-out;

    &:hover {
      fill: var(--global-hover-alt);
    }

    &:active {
      outline: none;
      fill: var(--global-hover-secondary);
    }
  }

  &__navigation {
    position: absolute;
    bottom: 121px;
    left: 169px;
  }
}

.story__swiper-pagination {
  display: contents;

  span {
    width: 6px;
    height: 6px;
  }
}

@media (max-width: 1440px) {
  .story {
    &__swiper {
      padding: 30px 0 30px 30px;
      min-height: 300px;
      height: 100%;
    }

    &__text {
      font-size: 30px;
    }

    &__navigation {
      bottom: 13px;
      left: 25px;
    }
  }
}

@media (max-width: 1000px) {
  .story {
    &__left {
      max-width: 380px;
    }
  }
}

@media (max-width: 768px) {
  .story {
    &__title {
      font-size: 40px;
    }

    &__text {
      font-size: 18px;
    }
  }
}

@media (max-width: 565px) {
  .story {
    &__left {
      max-width: 580px;
    }

    &__right {
      display: none;
    }
  }
}
