.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  row-gap: 30px;
  padding: 0;

  &__item {
    width: 46%;
    border: 1px solid #ececec;
    border-radius: 0 0 20px 20px;
    background-color: #fff;
  }

  &__item--bnone {
    width: 30%;
    border: 0;
  }

  &__item--width {
    width: 50%;
    border: 0;
  }
}

.card {
  display: flex;
  flex-direction: column;
  height: 100%;

  &__image--bgr {
    width: 200px;
    max-width: 100%;
    height: 160px;
    background-repeat: no-repeat;
  }

  &__content {
    padding: 30px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    flex-grow: 1;
  }

  &__content--pad {
    padding: 0;
    padding-top: 16px;
  }

  &__title {
    margin: 0;
  }

  &__descr {
    margin-top: 10px;
    margin-bottom: 30px;
  }

  &__link {
    margin-top: auto;
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 20px;
    color: var(--global-primary);
    position: relative;
    max-width: 145px;
    transition: outline 0.3s ease-in-out, color 0.3s ease-in-out;

    svg {
      width: 5px;
      height: 10px;
      fill: var(--global-primary);
      transition: fill 0.3s ease-in-out;
    }

    &:focus {
      outline: 2px solid var(--global-primary);
    }

    &:hover {
      outline: none;
      color: var(--global-hover-alt);

      svg {
        fill: var(--global-hover-alt);
      }
    }

    &:active {
      outline: none;
      color: var(--global-hover-secondary);

      svg {
        fill: var(--global-hover-secondary);
      }
    }
  }

  &__svg {
    width: 44px;
    height: 44px;
  }

  .m0 {
    margin-bottom: 0;
  }

  .bord-none {
    border: 0;
  }
}

@media (max-width: 1000px) {
  .cards {
    &__item--width {
      width: 43%;
      border: 0;
    }
  }
}

@media (max-width: 768px) {
  .cards {
    &__item--bnone {
      width: 50%;
    }
  }
}

@media (max-width: 564px) {
  .cards {
    &__item {
      width: 100%;
    }
  }

  .card {
    &__content--pad {
      padding: 0;
      padding-top: 0;
      align-items: center;
    }

    .img-centr {
      display: flex;
      justify-content: center;
    }
  }

  .partners {
    svg {
      max-width: 83px;
    }
  }
}
