.tutorial{
  margin-top: 180px;

  &__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__img {
    max-width: 400px;
    position: relative;
  }

  &__context {
    position: absolute;
    bottom: 30px;
    left: 30px;
    max-width: 200px;
  }

  &__span {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    letter-spacing: -0.02em;
    color: var(--base-text-on-dark);
  }

  &__block {
    max-width: 635px;
  }

  &__text {
    margin-top: 30px;
  }

  &__player{
    margin-top: 30px;
    display: flex;
    align-items: center;
    column-gap: 15px;
  }

  &__btn {
    height: 48px;
    width: 48px;

    .svg{
      width: 25px;
      height: 25px;
      border: 1px solid var(--base-text-primary);
      border-radius: 50px;
    }
  }

  &__play {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 18px;
    line-height: 156%;
    letter-spacing: -0.02em;
    color: var(--base-text-primary);
  }
}

@media (max-width: 1440px) {
  .tutorial{

    &__block {
      max-width: 400px;
    }

    &__text {
      margin-top: 10px;
    }

    &__player{
      margin-top: 10px;
    }
  }
}

@media (max-width: 1000px) {
  .tutorial{

    &__img{
      max-width: 350px;
    }

    &__block {
      max-width: 350px;
    }

    &__title{
      font-size: 42px;
    }
  }
}

@media (max-width: 768px) {
  .tutorial{
    margin-top: 75px;

    &__img{
      display: none;
    }

    &__context{
      display: none;
    }

    &__block {
      max-width: 768px;
    }
  }
}

