.logo {
  font-family: var(--second-family);
  font-weight: 800;
  font-size: 24px;
  line-height: 100%;
  color: inherit;
  transition: outline 0.3s ease-in-out, color 0.3s ease-in-out;

  &:focus {
    outline: 1px solid var(--global-hover-primary);
  }

  &:hover {
    color: var(--global-hover-alt);
  }

  &:active {
    outline: none;
    color: var(--global-hover-secondary);
  }
}

.next-link {
  display: flex;
  align-items: center;
  column-gap: 10px;
  border-radius: 1px;
  padding: 5px 20px;
  max-width: 172px;
  min-height: 32px;
  background: var(--global-primary);
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  line-height: 157%;
  letter-spacing: -0.02em;
  color: var(--base-text-on-dark);
  transition: outline 0.3s ease-in-out, color 0.3s ease-in-out,
    background 0.3s ease-in-out;

  &:focus {
    outline: 1px solid var(--global-primary);
    background: var(--global-hover-secondary);

    .svg-next {
      background: var(--global-hover-secondary);
    }
  }

  &:hover {
    color: var(--global-primary);
    background: var(--base-text-on-dark);

    .svg-next {
      fill: var(--global-primary);
      background: var(--base-text-on-dark);
      transform: translateX(10px);
    }
  }

  &:active {
    outline: none;
    color: var(--global-hover-alt);

    .svg-next {
      fill: var(--global-hover-alt);
    }
  }

  .svg-next {
    width: 20px;
    height: 10px;
    background: var(--global-primary);
    fill: white;
    transition: fill 0.3s ease-in-out, transform 0.3s ease-in-out,
      background 0.3s ease-in-out, outline 0.3s ease-in-out;
  }
}

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper-button-next,
.swiper-button-prev {
  top: 0;
}

.swiper-button-prev,
.swiper-rtl .swiper-button-next {
  width: 65px;
  height: 65px;
  border-radius: 100%;
  background: var(--base-bg-secondary);

  &:hover {
    background: var(--global-primary);
  }

  &:active {
    background: var(--global-alt);
  }
}

.swiper-button-next,
.swiper-rtl .swiper-button-prev {
  width: 65px;
  height: 65px;
  border-radius: 100%;
  background: var(--base-bg-secondary);

  &:hover {
    background: var(--global-primary);
  }

  &:active {
    background: var(--global-alt);
  }
}

.swiper-button-prev:after,
.swiper-rtl .swiper-button-next:after {
  content: url(../img/next-3.svg);
  width: 32px;
  height: 43px;
}

.swiper-button-next:after,
.swiper-rtl .swiper-button-prev:after {
  content: url(../img/next-3.svg);
  width: 32px;
  height: 43px;
  transform: rotate(180deg);
}

.nav {
  &__list {
    display: flex;
    flex-wrap: wrap;
    row-gap: 15px;
    column-gap: 30px;

    &--end {
      justify-content: flex-end;
    }
  }

  &__link {
    width: 100%;
    transition: outline 0.3s ease-in-out, color 0.3s ease-in-out;

    &:focus {
      outline: 1px solid var(--global-hover-primary);
    }

    &:hover {
      color: var(--global-hover-alt);
    }

    &:active {
      color: var(--global-hover-secondary);
    }
  }

  &__list--menu {
    display: block;
  }

  &__link {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 162%;
    letter-spacing: -0.02em;
    color: inherit;
  }

  &__link--menu {
    font-size: 25px;
  }
}

.section {
  margin-top: 60px;
}

.title-1 {
  margin: 0;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 64px;
  line-height: 112%;
  letter-spacing: -0.02em;
  text-transform: capitalize;
  color: inherit;
}

.title-2 {
  margin: 0;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 52px;
  line-height: 119%;
  letter-spacing: -0.02em;
  color: var(--base-text-primary);
}

.title-4 {
  margin: 0;
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 28px;
  color: #000;
}

.title-3 {
  margin: 0;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 20px;
  line-height: 150%;
  letter-spacing: -0.02em;
  color: var(--base-text-primary);
}

.text-big {
  margin: 0;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 20px;
  line-height: 150%;
  letter-spacing: -0.02em;
  color: inherit;
}

.text-normal {
  margin: 0;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 162%;
  letter-spacing: -0.02em;
  color: inherit;
}

.partners {
  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    margin: 0;

    svg {
      max-width: 130px;
      height: 40px;
      outline: none;
    }
  }

  &__item {
    height: 50px;
  }

  &__link {
    &:focus {
      outline: 1px solid var(--global-hover-primary);
    }
  }

  &__svg {
    width: 100%;
    max-width: 100px;
    height: 30px;
    fill: var(--base-text-alt);
    transition: fill 0.3s ease-in-out;

    &:hover {
      fill: var(--global-hover-alt);
    }

    &:active {
      outline: none;
      fill: var(--global-hover-secondary);
    }
  }
}

.partners__list--flex {
  gap: 30px;
  justify-content: center;
}

.partners__item--centr {
  padding-top: 7px;
}

.partners__item--width {
  max-width: 130px;
}

.business-list {
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  row-gap: 50px;

  &__text {
    margin-top: 8px;
  }

  &__item {
    padding-left: 78px;
  }

  &__item--pad {
    padding-left: 112px;
    max-width: 525px;
  }

  &__item--top {
    padding: 0;
    padding-top: 40px;
    max-width: 277px;
    width: 100%;
    background-image: url(../img/process2.svg);
    background-repeat: no-repeat;
  }

  &__item--finish {
    padding: 0;
    padding-top: 40px;
    max-width: 277px;
    width: 100%;
    background-image: url(../img/process1.svg);
    background-repeat: no-repeat;
  }
}

.bg-1 {
  background-image: url(../img/info1.svg);
  background-repeat: no-repeat;
}

.bg-2 {
  background-image: url(../img/info2.svg);
  background-repeat: no-repeat;
}

.bg-3 {
  background-image: url(../img/info3.svg);
  background-repeat: no-repeat;
}

.bg-4 {
  background-image: url(../img/work1.svg);
  background-repeat: no-repeat;
}

.bg-5 {
  background-image: url(../img/work2.svg);
  background-repeat: no-repeat;
}

.bg-6 {
  background-image: url(../img/work3.svg);
  background-repeat: no-repeat;
}

.bg-7 {
  background-image: url(../img/work4.svg);
  background-repeat: no-repeat;
}

.centr {
  align-items: center;
}

.bgr-1 {
  background: url(../img/css.svg);
}

.bgr-2 {
  background: url(../img/content.svg);
}

.bgr-3 {
  background: url(../img/launch.svg);
}

@media (max-width: 1000px) {
  .title-1 {
    font-size: 45px;
  }
}

@media (max-width: 768px) {
  .partners {
    &__list {
      justify-content: space-between;
    }
  }

  .partners__list--flex {
    justify-content: center;
  }
}

@media (max-width: 564px) {
  .swiper-button-prev,
  .swiper-rtl .swiper-button-next {
    width: 40px;
    height: 40px;
  }

  .swiper-button-next,
  .swiper-rtl .swiper-button-prev {
    width: 40px;
    height: 40px;
  }

  .next-link {
    padding: 5px;
  }

  .title-1 {
    font-size: 30px;
  }

  .title-2 {
    font-size: 25px;
  }

  .text-big {
    font-size: 17px;
  }

  .business-list {
    &__item--pad {
      padding-left: 90px;
    }
  }
}
