.feature {
  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__list {
    max-width: 590px;
    justify-content: space-between;
    padding: 0;
    margin: 0;
  }
}

@media (max-width: 1000px) {
  .feature {
    &__list {
      max-width: 1000px;
      gap: 15px;
    }

    &__img {
      display: none;
    }
  }
}

@media (max-width: 564px) {
  .feature {
    &__list {
      justify-content: normal;
    }
  }
}
