.services{
  margin: 0;
  padding: 135px 0 100px;
  background: var(--base-bg-secondary);

  &__content{
    padding: 0 2px;
    max-width: 930px;
  }

  &__text {
    margin: 0;
    margin-top: 30px;
    font-family: var(--font-family);
    font-style: italic;
    font-weight: 400;
    font-size: 24px;
    line-height: 142%;
    letter-spacing: -0.02em;
    color: #000;
  }

  &__link {
    margin-top: 36px;
    padding: 15px 25px;
    max-width: 262px;
    height: 56px;
    margin-bottom: 2px;
  }
}

.person {
  display: flex;
  column-gap: 25px;
  align-items: center;
  margin-top: 35px;
  max-width: 300px;

  &__img{

    img{
      max-width: 90px;
      width: 90px;
    }
  }

  &__info{
    width: 100%;
  }

  &__title {
    margin: 0;
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 18px;
    line-height: 156%;
    letter-spacing: -0.02em;
    color: #000;
  }

  &__text {
    margin: 0;
  }
}

@media (max-width: 564px) {
  .services{
    padding: 75px 2px 75px;
  }
}
