.hero{

  &__content{
    display: flex;
    justify-content: space-between;
    column-gap: 15px;
    padding: 0 2px;
  }

  &__left{
    max-width: 635px;
    width: 100%;
  }

  &__right{
    max-width: 460px;
    width: 100%;

    img{
      height: 100%;
    }
  }

  &__text{
    margin: 0;
    margin-top: 20px;
  }

  &__navigation{
    margin-top: 42px;
    max-width: 440px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 20px;

    .text-normal{
      margin: 0;
      max-width: 250px;
    }
  }

  &__link{
    max-width: 200px;
    min-height: 56px;
  }

  &__partners{
    margin-top: 85px;
  }
}

@media (max-width: 1000px) {
  .hero{

    &__navigation{

      .text-normal{
        max-width: 180px;
      }
    }
  }
}

@media (max-width: 768px) {
  .hero{

    &__right{
      display: none;
    }

    &__partners{
      margin-top: 30px;
    }
  }
}

@media (max-width: 564px) {
  .hero{

    &__navigation{
      margin-top: 15px;

      .text-normal{
        display: none;
      }
    }
  }
}
