.office{
  margin-top: 220px;
  margin-bottom: 175px;

  &__block{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between
  }

  &__text{
    max-width: 300px;
    color: #64626a;
    margin-right: 10%;
  }

  &__cards{
    margin: 0;
    margin-top: 87px;
  }
}


@media (max-width: 1440px) {
  .office{
    margin: 100px 0;

    &__cards{
      margin-top: 40px;
    }
  }
}

@media (max-width: 768px) {
  .office{
    margin: 50px 0;

    &__text{
      margin-right: 0;
    }
  }
}

@media (max-width: 564px) {
  .office{

    &__block{
      flex-direction: column;
      align-items: baseline;
      row-gap: 15px;
    }
  }
}

