.manual{
  margin-top: 100px;
  margin-bottom: 100px;

  &__decor {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 24px;
    line-height: 142%;
    letter-spacing: -0.02em;
    color: var(--global-primary);
  }

  &__block {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    justify-content: space-between;
  }

  &__title {
    width: 100%;
    max-width: 760px;
  }

  &__block-btn {
    width: 165px;
    height: 50px;
    position: relative;
  }

  &__swiper{
    margin-top: 50px;
  }

  &__swiper-wrapper{
    padding: 0;
  }
}


@media (max-width: 1440px) {
  .manual{

    &__block-btn{
      margin-top: 50px;
    }


    &__swiper{
      margin-top: 20px;
    }
  }
}

@media (max-width: 564px) {
  .manual{

    &__block-btn {
      margin-top: 30px;
      width: 110px;
    }

    &__swiper {
      margin-top: 0;
    }
  }

}

