.statistics{
  margin: 0;
  padding: 75px 2px 65px;
  background: var(--global-primary);

  &__container{
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title{
    color: var(--base-text-on-dark);
  }

  &__text{
    margin-top: 8px;
    color: var(--base-text-on-dark);
  }


  &__list {
    padding: 0;
    margin: 0;
    margin-top: 55px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: 25px;
    row-gap: 15px;
  }

  &__item {
    max-width: 250px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    row-gap: 5px;

    &:not(:last-child){
      border-right: 1px solid #ffffff45;
    }
  }

  &__num {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 52px;
    line-height: 119%;
    letter-spacing: -0.02em;
    text-align: center;
    color: var(--base-text-on-dark);
  }

  &__span {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 156%;
    letter-spacing: -0.02em;
    text-align: center;
    color: var(--base-text-on-dark);
  }
}

@media (max-width: 1000px) {
  .statistics{

    &__item:not(:last-child){
      border-right: 0;
    }

    &__item:nth-child(1){
      border-right: 1px solid #ffffff45;
    }

    &__item:nth-child(3){
      border-right: 1px solid #ffffff45;
    }
  }
}

@media (max-width: 564px) {
  .statistics{

    &__list{
      margin-top: 30px;
    }

    &__item{
      max-width: 130px;
    }

    &__item:nth-child(1){
      border-right: 0;
    }

    &__item:nth-child(3){
      border-right: 0;
    }

    &__num{
      font-size: 28px;
    }

    &__span{
      font-size: 15px;
    }

  }
}







