.header {
  background-color: var(--base-bg-primary);

  &__container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    column-gap: 52px;
    row-gap: 20px;
    padding: 20px 2px;
  }

  &__logo {
    display: flex;
    align-items: center;
  }

  &__nav {
    width: 100%;
    max-width: 762px;
  }

  &__content {
    display: flex;
    align-items: center;
    column-gap: 10px;
  }
}

@media (max-width: 1440px) {
  .header {
    &__container {
      justify-content: space-between;
    }

    &__nav {
      width: 350px;
    }
  }

  .nav {
    &__list {
      row-gap: 0px;
    }
  }
}

@media (max-width: 1000px) {
  .header {
    &__nav {
      width: 250px;
    }
  }

  .nav {
    &__list {
      row-gap: 0px;
    }
  }
}

@media (max-width: 768px) {
  .header {
    &__content {
      flex-direction: row-reverse;
    }
  }
}

@media (max-width: 564px) {
  .header {
    &__logo {
      font-size: 21px;
    }

    &__container {
      column-gap: 0;
    }

    &__content {
      column-gap: 5px;
    }
  }

  .logo__svg {
    display: none;
  }
}
